
.nav-box{position:fixed;bottom:0;width:100%;background:#fff;border-top:1px solid #f0f0f0
}
.nav-box .nav-list{width:100%;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;z-index:10
}
.nav-box .nav-list .nav-item{-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1;color:#fff;height:40px;text-align:center;display:block;font-family:cursive;text-decoration:none
}
.nav-box .nav-list .nav-item i{width:90px;height:100%;display:block;background-repeat:no-repeat;background-size:120%;margin:0 auto;background-position-y:center;background-position-x:center;position:relative;top:-3px
}


